import * as PouchDB from 'pouchdb/dist/pouchdb';
import * as PouchCdvAdapter from 'pouchdb-adapter-cordova-sqlite/dist/pouchdb.cordova-sqlite';
import {IDatabase} from "../database/core.db";
import {RxSync} from "./RxSync";
import {RxChanges} from "./RxChanges";
import {RxCollection} from "./RxCollection";

PouchDB.plugin(PouchCdvAdapter);
declare let window: any;

export class RxDb {

  public dbLocal = null;
  public dbRemote = null;
  public rxSync = new RxSync();
  public rxChange = new RxChanges();
  public collections = {};

  constructor() {

  }

  public login(remote: string, local: string) {
    let opts = {
      remote: {skipSetup: true},
      local: {adapter: 'cordova-sqlite', revs_limit: 50, auto_compaction: true, size: 250}
    };

    if (!window.cordova) {
      delete opts.local.adapter;
    }

    this.dbRemote = new PouchDB(remote, opts.remote);
    this.dbLocal = new PouchDB(local, opts.local);
  }

  public logout() {
    // stop sync
    // stop changelistener
    // cleanup all collections
    // cleanup all syncs
  }

  public dump() {

  }

  public importDump() {

  }

  public async info(): Promise<IDatabase> {
    let data = await Promise.all([this.dbRemote.info(), this.dbLocal.info()]);
    return {remote: data[0], local: data[1]};
  }

  public sync(live: boolean, writePermission: boolean = true) {
    if (writePermission) {
      this.rxSync.setupListener(this.dbLocal.sync(this.dbRemote, {live: live, retry: true}));
    } else {
      this.rxSync.setupListener(this.dbRemote.replicate.to(this.dbLocal, {live: live, retry: true}));
    }
    return this.rxSync;
  }

  public changes() {
    const changeOpts = {
      live: true,
      include_docs: true,
      since: 'now',
      return_docs: false
    };
    this.rxChange.setupListener(this.dbLocal.changes(changeOpts));
    return this.rxChange;
  }

  // todo already have collections, doing this...? remove or proxy?
  public get(id: string): Promise<any> {
    return this.dbLocal.get(id);
  }

  public insert(doc: any): Promise<any> {
    return this.dbLocal.put(doc);
  }

  public remove(doc: any): Promise<any> {
    return this.dbLocal.remove(doc);
  }

  public collection(docType: string, user?: string): RxCollection<any> {
    if (this.collections[docType] == docType) {
      return this.collections[docType];
    }
    let collection = new RxCollection(this.dbLocal, this.rxChange.change$, docType, user);
    this.collections[docType] = collection;
    return collection;
  }

  /**
   * usage:
   *
   * document.original // private backup
   * doucment.doc // private doc, all props are proxied here, before proxying setter makes a original backup
   * document.restore()
   * document.save()
   * document.remove()
   * document.$.subscribe
   * document.$.update.subscribe
   * document.$.remove.subscribe
   *
   * db.order.$.subscribe
   * db.order.insert$.subscribe
   * db.order.update$.subscribe
   * db.order.remove$.subscribe
   * db.order.filtered$.subscribe
   * db.order.filterDirty$.subscribe
   * db.order.setFilter(filter)
   * db.order.resetFilter()
   * db.order.setSort(sort)
   * db.order.resetSort()
   * db.order.dump()
   * db.order.importDump()
   *
   * db.uploads$
   * db.downloads$
   * db.completedPull$
   * db.completedPush$
   * db.completed$
   * db.live$
   * db.lastSync$
   * db.status$
   * db.writePermission$
   * db.user???
   * db.isTest
   *
   * db.insert(doc)
   * db.remove(id|doc)
   * db.dump()
   * db.importDump()
   * db.destroy // free up memory
   * db.sync // returns RXSyncer
   *
   * db.login()
   * db.logout() // Destory
   *
   * syncer.change$.subscribe
   * syncer.docs$.subscibe
   * syncer.active$.subscribe
   * syncer.complete$.subscribe
   * syncer.error$.subscribe
   * syncer.cancel()
   *
   */
}