import {filter} from "rxjs/operators";
import {BehaviorSubject, Observable, Subscription} from "rxjs";
import {RxStore} from "./RxStore";
import {IModel} from "./interfaces/IModel";

export class RxCollection<T extends IModel> {

  private _subs: Subscription[] = [];
  private docsSubject: BehaviorSubject<T[]> = new BehaviorSubject([]);
  private rxStore: RxStore<T> = new RxStore(this.docsSubject);

  public changes$;
  public insert$;
  public update$;
  public remove$;
  public docs$: Observable<T[]> = this.docsSubject.asObservable();

  // setFilter(observable)
  // option 1 run through all docs, on change
  // option 2 run the change on through filter and append or remove docs | requires 3 subscriptions/but still need to sort

  // todo change user to observable
  constructor(private localPouch, private allChanges$, private docType: string, private user?: any) {

    // ------------------------------------------
    // create changes$, insert$, update$, remove$
    // ------------------------------------------
    this.changes$ = this.allChanges$.pipe(
      filter((change: any) => change.doc.type === this.docType || change.id.startsWith(this.docType))
    );

    this.insert$ = this.changes$.pipe(
      filter((change: any) => change.op === 'INSERT')
    );

    this.update$ = this.changes$.pipe(
      filter((change: any) => change.op === 'UPDATE')
    );

    this.remove$ = this.changes$.pipe(
      filter((change: any) => change.op === 'REMOVE')
    );

    // ------------------------------------------
    // create docs$
    // ------------------------------------------
    // todo constuctor async better way?
    this.loadData().then(res => {
      this.rxStore.setData(res);
    });

    this._subs.push(
      this.insert$.subscribe(next => {
        this.rxStore.addToStore(next.doc)
      })
    );

    this._subs.push(
      this.update$.subscribe(next => {
        this.rxStore.updateInStore(next.doc)
      })
    );

    this._subs.push(
      this.remove$.subscribe(next => {
        this.rxStore.removeFromStore(next.doc)
      })
    );
  }

  public destroy() {
    this._subs.forEach(sub => sub.unsubscribe());
    this.rxStore = null;
  }

  // ------------------------------------------
  // loadData
  // ------------------------------------------
  private async loadData(): Promise<any[]> {
    let endkey = this.docType + '-\uffff';
    if (this.user) {
      endkey = this.docType + '-' + this.user.code + '\uffff'
    }

    let res = await this.localPouch.allDocs({
      startkey: this.docType,
      endkey: endkey,
      include_docs: true
    });

    return res.rows.map(row => {
      return row.doc;
    });
  }

  // ------------------------------------------
  // save, remove
  // ------------------------------------------
  public get(id: string): Promise<any> {
    return this.localPouch.get(id);
  }

  public saveId(id: string): Promise<any> {
    let doc = this.rxStore.get(id);
    return this.insert(doc);
  }

  public insert(doc: T): Promise<any> {
    return this.localPouch.save(doc);
  }

  public remove(doc: T): Promise<any> {
    return this.localPouch.local.remove(doc);
  }
}